module.exports = "/*\nImport\n=========================== */\n/* -------------------------------------------------------------\n  Sass CSS3 Mixins! The Cross-Browser CSS3 Sass Library\n  By: Matthieu Aussaguel, http://www.mynameismatthieu.com, @matthieu_tweets\n\n  List of CSS3 Sass Mixins File to be @imported and @included as you need\n\n  The purpose of this library is to facilitate the use of CSS3 on different browsers avoiding HARD TO READ and NEVER\n  ENDING css files\n\n  note: All CSS3 Properties are being supported by Safari 5\n  more info: http://www.findmebyip.com/litmus/#css3-properties\n\n------------------------------------------------------------- */\n/*\nColor\n=========================== */\n/*\nFont Family\n=========================== */\n.material-icons {\n  font-family: \"Material Icons\";\n  font-weight: normal;\n  line-height: 20px;\n}\n.material-icons {\n  font-family: \"Material Icons\";\n  font-weight: normal;\n  line-height: 20px;\n}\n/*\nLight Box\n=========================== */\n.wrap-lightbox {\n  position: fixed;\n  z-index: 999999999999999;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n}\n.wrap-lightbox:before {\n  content: \"\";\n  position: absolute;\n  z-index: 1;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background-color: #fff;\n}\n.wrap-lightbox .close {\n  position: absolute;\n  top: 25px;\n  right: 30px;\n  z-index: 3;\n}\n.wrap-lightbox .close mat-icon {\n  position: relative;\n  top: 3px;\n}\n.wrap-lightbox .content.valign-wrapper {\n  width: 100%;\n  position: relative;\n  z-index: 2;\n}\n.wrap-lightbox .content.valign-wrapper .nav {\n  position: absolute;\n  top: 50%;\n  margin-top: -20px;\n  z-index: 2;\n  padding-top: 2px;\n}\n.wrap-lightbox .content.valign-wrapper .nav.left {\n  left: 30px;\n}\n.wrap-lightbox .content.valign-wrapper .nav.right {\n  right: 30px;\n}\n.wrap-lightbox .content.valign-wrapper .wrap-img {\n  display: block;\n  margin: auto;\n  position: relative;\n  max-width: calc(100vw - 200px);\n  z-index: 1;\n}\n.wrap-lightbox .content.valign-wrapper .wrap-img img {\n  width: 100%;\n}\n.wrap-lightbox .description {\n  position: absolute;\n  z-index: 3;\n  bottom: 0;\n  left: 0;\n  width: 100%;\n}\n.wrap-lightbox .description .content {\n  padding: 15px 20px;\n  text-align: center;\n  display: table;\n  margin: auto;\n  background: #333;\n  -ms-box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.2);\n  -o-box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.2);\n  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.2);\n}\n.wrap-lightbox .description .content .title {\n  margin-bottom: 0;\n  color: #fff;\n  font-weight: 400;\n  text-transform: capitalize;\n}\n.wrap-lightbox .mat-button, .wrap-lightbox .mat-fab, .wrap-lightbox .mat-icon-button, .wrap-lightbox .mat-mini-fab, .wrap-lightbox .mat-raised-button {\n  font-size: 16px !important;\n}\n.wrap-lightbox .spinner {\n  display: table;\n  margin: auto;\n}\n.wrap-lightbox .spinner mat-spinner {\n  width: 50px;\n  height: 50px;\n  display: block;\n}\n/*\nResponsive\n=========================== */\n@media (max-width: 767px) {\n  .wrap-lightbox .close {\n    top: 10px;\n    right: 15px;\n  }\n  .wrap-lightbox .content.valign-wrapper .nav.left {\n    left: 15px;\n  }\n  .wrap-lightbox .content.valign-wrapper .nav.right {\n    right: 15px;\n  }\n  .wrap-lightbox .content.valign-wrapper .wrap-img {\n    max-width: 300px !important;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"