module.exports = "/*\nImport\n=========================== */\n/* -------------------------------------------------------------\n  Sass CSS3 Mixins! The Cross-Browser CSS3 Sass Library\n  By: Matthieu Aussaguel, http://www.mynameismatthieu.com, @matthieu_tweets\n\n  List of CSS3 Sass Mixins File to be @imported and @included as you need\n\n  The purpose of this library is to facilitate the use of CSS3 on different browsers avoiding HARD TO READ and NEVER\n  ENDING css files\n\n  note: All CSS3 Properties are being supported by Safari 5\n  more info: http://www.findmebyip.com/litmus/#css3-properties\n\n------------------------------------------------------------- */\n/*\nColor\n=========================== */\n/*\nFont Family\n=========================== */\n.material-icons {\n  font-family: \"Material Icons\";\n  font-weight: normal;\n  line-height: 20px;\n}\n.material-icons {\n  font-family: \"Material Icons\";\n  font-weight: normal;\n  line-height: 20px;\n}\n/*\nVariable\n=========================== */\n/*\nSide\n=========================== */\n.side {\n  width: 125px;\n  height: 100%;\n  display: table;\n  position: fixed;\n  left: 0;\n  top: 0;\n  background: #1e2327;\n  z-index: 2;\n}\n.side .wrap-brand {\n  display: block;\n  width: 100%;\n  position: absolute;\n  top: 0;\n  left: 0;\n  padding: 20px 0 15px 0;\n  text-align: center;\n  background: #1e2327;\n  -webkit-transition: all 0.5s ease-in-out;\n  transition: all 0.5s ease-in-out;\n  z-index: 9;\n}\n.side .wrap-brand img {\n  width: 78px;\n  margin-bottom: 0;\n}\n.side .wrap-brand img.mobile {\n  display: none;\n}\n.side nav {\n  display: table-cell;\n  vertical-align: middle;\n}\n.side nav li {\n  width: 100%;\n  float: none;\n  text-align: center;\n  position: relative;\n}\n.side nav li a {\n  font-size: 24px;\n  display: block;\n  width: 100%;\n  padding: 15px;\n  border-radius: 0;\n  box-shadow: none !important;\n  position: relative;\n}\n.side nav li a.active {\n  background: rgba(0, 0, 0, 0.2);\n}\n.side nav li a .badge {\n  position: absolute;\n  display: table;\n  padding: 2px 5px 0;\n  color: #fff;\n  border: solid 1px #1e2327;\n  left: 50%;\n  top: 7px;\n  height: 10px;\n  border-radius: 4px;\n  min-width: 0;\n  -webkit-transform: scale(0.8, 0.8);\n          transform: scale(0.8, 0.8);\n}\n/*\nMenu\n=========================== */\n.wrap-menu {\n  width: 270px;\n  background: #262a2e;\n  position: fixed;\n  height: 100%;\n  left: -270px;\n  top: 0;\n  overflow: auto;\n  -webkit-transition: all 0.5s ease-in-out;\n  transition: all 0.5s ease-in-out;\n  color: #ddd;\n  z-index: 1;\n}\n.wrap-menu nav {\n  width: 100%;\n  padding: 25px 30px;\n  display: table;\n}\n.wrap-menu nav ul.menu {\n  display: table-cell;\n  vertical-align: middle;\n}\n.wrap-menu nav ul.menu > li {\n  width: 100%;\n  float: none;\n  height: auto;\n  padding: 0;\n}\n.wrap-menu nav ul.menu > li > a {\n  text-transform: uppercase;\n  display: block;\n  width: 100%;\n  padding: 5px 0;\n  font-size: 16px;\n}\n.wrap-menu nav ul.menu > li > a:hover {\n  background: transparent;\n  text-decoration: none;\n}\n.wrap-menu nav ul.menu ul.drop-down {\n  padding: 5px 0;\n  margin: 0;\n}\n.wrap-menu nav ul.menu ul.drop-down li {\n  width: 100%;\n  float: none;\n}\n.wrap-menu nav ul.menu ul.drop-down li a {\n  color: #999;\n  background: transparent;\n  text-decoration: none;\n}\n/*\nResponsive\n=========================== */\n@media (max-width: 767px) {\n  .side {\n    position: relative;\n    width: 100%;\n    height: auto;\n    display: block;\n    overflow: hidden;\n    z-index: 5;\n  }\n  .side nav {\n    display: block;\n    text-align: center;\n    position: relative;\n    z-index: 1;\n    padding: 0;\n    height: 53px;\n  }\n  .side nav ul {\n    display: inline-block;\n  }\n  .side nav ul li {\n    width: auto;\n    float: left;\n    display: block;\n  }\n  .side .mat-button, .side .mat-fab, .side .mat-icon-button, .side .mat-mini-fab, .side .mat-raised-button {\n    min-width: 35px;\n    line-height: 24px;\n  }\n  .side .wrap-brand {\n    background: #f9f9f9;\n    overflow: hidden;\n    position: relative;\n    top: 0;\n    padding-top: 14px;\n    margin-top: 0px;\n    text-align: center;\n    z-index: 2;\n  }\n  .side .wrap-brand img {\n    width: 50px;\n    display: none;\n  }\n  .side .wrap-brand img.mobile {\n    display: block;\n    margin: auto;\n  }\n\n  .wrap-menu {\n    width: 100%;\n    display: block;\n    left: 0;\n    top: -100%;\n    height: 225px;\n    z-index: 4;\n    position: absolute;\n    -webkit-transition: all 0s ease-in-out;\n    transition: all 0s ease-in-out;\n  }\n  .wrap-menu nav {\n    height: auto !important;\n    display: block;\n    text-align: center;\n  }\n  .wrap-menu nav ul.menu {\n    display: block;\n    width: 100%;\n  }\n  .wrap-menu nav ul.menu > li > a {\n    font-size: 14px;\n    padding-top: 5px;\n    padding-bottom: 5px;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"