module.exports = "/*\nImport\n=========================== */\n/* -------------------------------------------------------------\n  Sass CSS3 Mixins! The Cross-Browser CSS3 Sass Library\n  By: Matthieu Aussaguel, http://www.mynameismatthieu.com, @matthieu_tweets\n\n  List of CSS3 Sass Mixins File to be @imported and @included as you need\n\n  The purpose of this library is to facilitate the use of CSS3 on different browsers avoiding HARD TO READ and NEVER\n  ENDING css files\n\n  note: All CSS3 Properties are being supported by Safari 5\n  more info: http://www.findmebyip.com/litmus/#css3-properties\n\n------------------------------------------------------------- */\n/*\nColor\n=========================== */\n/*\nFont Family\n=========================== */\n.material-icons {\n  font-family: \"Material Icons\";\n  font-weight: normal;\n  line-height: 20px;\n}\n.material-icons {\n  font-family: \"Material Icons\";\n  font-weight: normal;\n  line-height: 20px;\n}\n/*\nItem Product\n=========================== */\n.reg-price {\n  margin-top: 10px;\n  font-size: 12px;\n}\n.row-product {\n  border: 0;\n  position: relative;\n  z-index: 1;\n  border-top: solid 1px transparent;\n}\n.item-product {\n  position: relative;\n  z-index: 1;\n  border: solid 1px #efefef;\n  margin: -0.5px;\n  background: #fff;\n  text-align: center;\n  -webkit-transition: all 0.5s ease-in-out;\n  transition: all 0.5s ease-in-out;\n}\n.item-product a.close {\n  position: absolute;\n  top: 10px;\n  right: 10px;\n  width: 29px !important;\n  height: 29px !important;\n  filter: alpha(opacity=50);\n  -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=50)\";\n  -webkit-opacity: 0.5;\n  -khtml-opacity: 0.5;\n  -moz-opacity: 0.5;\n  -ms-opacity: 0.5;\n  -o-opacity: 0.5;\n  opacity: 0.5;\n  background: #fff !important;\n  color: #8f8f8f;\n  border: solid 1px #efefef;\n}\n.item-product a.close .material-icons.mat-icon {\n  padding: 0;\n  position: relative;\n  top: -4px;\n}\n.item-product a.close:hover {\n  filter: alpha(opacity=100);\n  -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\n  -webkit-opacity: 1;\n  -khtml-opacity: 1;\n  -moz-opacity: 1;\n  -ms-opacity: 1;\n  -o-opacity: 1;\n  opacity: 1;\n}\n.item-product:hover {\n  background: #fff;\n}\n.item-product img {\n  width: 100%;\n  display: block;\n  margin: auto;\n}\n.item-product .link-full {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n}\n.item-product .title {\n  margin-bottom: 0;\n  margin-top: 0;\n  text-transform: capitalize;\n}\n.item-product p {\n  margin-bottom: 0;\n  color: #9f9f9f;\n}\n.item-product .price {\n  font-family: sans-serif;\n}\n.item-product .content {\n  position: relative;\n  padding: 15px 20px;\n}\n.item-product .content .overlay {\n  border-top: 0;\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: #fff;\n}\n.item-product .content .overlay ul {\n  padding: 0;\n  margin: 0;\n  list-style: none;\n  display: inline-block;\n  margin: 0 auto -7px auto;\n}\n.item-product .content .overlay ul li {\n  float: left;\n  display: block;\n  margin: 0 2px;\n}\n.item-product .content .overlay ul li .mat-button-wrapper {\n  position: relative;\n  top: 5px;\n}\n.item-product .content .overlay ul li .mat-button-wrapper .material-icons.mat-icon {\n  padding: 0;\n}\n.item-product .content .overlay .material-icons.mat-icon {\n  margin-top: 2px;\n}\n.item-product .content .overlay .mat-button, .item-product .content .overlay .mat-fab, .item-product .content .overlay .mat-icon-button, .item-product .content .overlay .mat-mini-fab, .item-product .content .overlay .mat-raised-button {\n  font-size: 16px;\n}\n/*\nEmpty product\n=========================== */\n.empty-product {\n  padding: 100px 0;\n  text-align: center;\n  color: #999;\n}\n.empty-product h1, .empty-product h2, .empty-product h3, .empty-product h4, .empty-product h5, .empty-product h6 {\n  color: #999;\n  text-transform: none;\n}\n/*\nPagination\n=========================== */\n.wrap-pagination {\n  padding: 25px 0 30px 0;\n}\n.wrap-pagination pagination-controls {\n  display: table;\n  margin: auto;\n}\n/*\nLoading\n=========================== */\n.valign-wrapper {\n  position: relative;\n  width: 100%;\n  top: 0;\n  right: 0;\n  background: #fff;\n}\n.valign-wrapper .mat-progress-spinner {\n  display: block;\n  margin: auto;\n}\n/*\nResponsive\n=========================== */\n@media (max-width: 480px) {\n  .row-product > .col {\n    margin-bottom: 25px;\n  }\n  .row-product > .col:last-child {\n    margin-bottom: 0;\n  }\n}\n@media (max-width: 415px) {\n  .row-product .col {\n    width: 100% !important;\n  }\n  .row-product .col .item-product {\n    height: auto !important;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"