module.exports = "/*\nImport\n=========================== */\n/* -------------------------------------------------------------\n  Sass CSS3 Mixins! The Cross-Browser CSS3 Sass Library\n  By: Matthieu Aussaguel, http://www.mynameismatthieu.com, @matthieu_tweets\n\n  List of CSS3 Sass Mixins File to be @imported and @included as you need\n\n  The purpose of this library is to facilitate the use of CSS3 on different browsers avoiding HARD TO READ and NEVER\n  ENDING css files\n\n  note: All CSS3 Properties are being supported by Safari 5\n  more info: http://www.findmebyip.com/litmus/#css3-properties\n\n------------------------------------------------------------- */\n/*\nColor\n=========================== */\n/*\nFont Family\n=========================== */\n.material-icons {\n  font-family: \"Material Icons\";\n  font-weight: normal;\n  line-height: 20px;\n}\n.material-icons {\n  font-family: \"Material Icons\";\n  font-weight: normal;\n  line-height: 20px;\n}\n/*\nCompare Product\n=========================== */\n.compare-product > .box-page > .row {\n  display: block;\n  margin: 0;\n}\n.compare-product > .box-page > .row > .col {\n  padding: 0;\n}\n.compare-product > .box-page > .row > .col > .row {\n  margin: 0;\n  padding: 0;\n}\n.compare-product > .box-page > .row > .col > .row .col {\n  padding: 0 1px;\n}\n.compare-product .wrap-table {\n  width: 100%;\n}\n.compare-product table.table-compare {\n  margin: 0;\n  border: solid 1px #efefef;\n  width: auto;\n}\n.compare-product table.table-compare tr td {\n  border-left: solid 1px #efefef;\n  border-right: solid 1px #efefef;\n  border-bottom: dashed 1px #efefef;\n  padding: 15px 20px;\n  vertical-align: top;\n  text-align: center;\n  -webkit-transition: all 0.5s ease-in-out;\n  transition: all 0.5s ease-in-out;\n}\n.compare-product table.table-compare tr td:first-child {\n  font-weight: bold;\n}\n.compare-product table.table-compare tr td.empty {\n  text-align: center;\n  vertical-align: middle;\n  width: 100%;\n}\n.compare-product table.table-compare tr td.empty .title {\n  margin-bottom: 0;\n  margin-top: 20px;\n  text-transform: none;\n  color: #999;\n}\n.compare-product table.table-compare tr td.info {\n  width: 200px;\n  max-width: 200px;\n  min-width: 200px;\n  text-align: center;\n  background: #fff;\n  vertical-align: middle;\n  padding: 25px 30px;\n}\n.compare-product table.table-compare tr td.info p {\n  font-weight: normal;\n}\n.compare-product table.table-compare tr td.info .counter {\n  margin-bottom: 5px;\n  letter-spacing: 0;\n  text-transform: none;\n}\n.compare-product table.table-compare tr td.info button {\n  width: 30px;\n  height: 30px;\n}\n.compare-product table.table-compare tr td.info button mat-icon {\n  position: relative;\n  top: -2px;\n}\n.compare-product table.table-compare tr td.wrap-img {\n  position: relative;\n  padding: 0;\n  overflow: hidden;\n}\n.compare-product table.table-compare tr td.wrap-img img {\n  width: 100%;\n  margin-bottom: -10px;\n}\n.compare-product table.table-compare tr td.wrap-img a.close {\n  position: absolute;\n  top: 10px;\n  right: 10px;\n  width: 29px !important;\n  height: 29px !important;\n  filter: alpha(opacity=50);\n  -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=50)\";\n  -webkit-opacity: 0.5;\n  -khtml-opacity: 0.5;\n  -moz-opacity: 0.5;\n  -ms-opacity: 0.5;\n  -o-opacity: 0.5;\n  opacity: 0.5;\n  background: #fff !important;\n  color: #8f8f8f;\n  border: solid 1px #efefef;\n}\n.compare-product table.table-compare tr td.wrap-img a.close .material-icons.mat-icon {\n  padding: 0;\n  position: relative;\n  top: -4px;\n}\n.compare-product table.table-compare tr td.wrap-img a.close:hover {\n  filter: alpha(opacity=100);\n  -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\n  -webkit-opacity: 1;\n  -khtml-opacity: 1;\n  -moz-opacity: 1;\n  -ms-opacity: 1;\n  -o-opacity: 1;\n  opacity: 1;\n}\n.compare-product table.table-compare tr td .title {\n  margin-bottom: 5px;\n  text-transform: capitalize;\n  padding-left: 20px;\n  padding-right: 20px;\n  text-transform: capitalize;\n  text-align: center;\n}\n.compare-product table.table-compare tr td .title a {\n  text-decoration: none !important;\n  color: #3f3f3f;\n}\n.compare-product table.table-compare tr td p:last-child {\n  margin-bottom: 0;\n}\n/*\nLoading\n=========================== */\n.valign-wrapper {\n  position: relative;\n  width: 100%;\n  top: 0;\n  right: 0;\n  background: #fff;\n}\n.valign-wrapper mat-spinner {\n  width: 50px;\n  height: 50px;\n  display: block;\n  margin: auto;\n}\n/*\nInput Slider \n=========================== */\n.wrap-slider {\n  padding-left: 180px;\n  margin-bottom: 0;\n  margin-top: 15px;\n}\n.wrap-slider mat-slider {\n  width: 200px;\n  display: block;\n  margin: auto;\n}\n.wrap-slider small {\n  display: table;\n  margin: auto;\n  margin-top: -10px;\n  margin-bottom: 0;\n}\n/*\nResponsive\n=========================== */\n@media (max-width: 480px) {\n  table.table-compare tr td.info {\n    width: 130px !important;\n    max-width: 130px !important;\n    padding-right: 5px !important;\n    padding-left: 5px !important;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"