module.exports = "/*\nImport\n=========================== */\n/* -------------------------------------------------------------\n  Sass CSS3 Mixins! The Cross-Browser CSS3 Sass Library\n  By: Matthieu Aussaguel, http://www.mynameismatthieu.com, @matthieu_tweets\n\n  List of CSS3 Sass Mixins File to be @imported and @included as you need\n\n  The purpose of this library is to facilitate the use of CSS3 on different browsers avoiding HARD TO READ and NEVER\n  ENDING css files\n\n  note: All CSS3 Properties are being supported by Safari 5\n  more info: http://www.findmebyip.com/litmus/#css3-properties\n\n------------------------------------------------------------- */\n/*\nColor\n=========================== */\n/*\nFont Family\n=========================== */\n.material-icons {\n  font-family: \"Material Icons\";\n  font-weight: normal;\n  line-height: 20px;\n}\n.material-icons {\n  font-family: \"Material Icons\";\n  font-weight: normal;\n  line-height: 20px;\n}\n/*\nDetail Product\n=========================== */\n.m-b {\n  margin-bottom: 80px;\n}\n.add-subti {\n  font-size: 23px;\n  color: #636363;\n  margin-bottom: 36px;\n  font-weight: 100;\n}\n.margin-hr {\n  margin-bottom: 50px;\n}\n.desc {\n  font-size: 28px;\n  margin-top: 30px;\n  margin-bottom: 15px;\n}\n.text-adj {\n  font-size: 34px;\n  color: #de1438;\n}\n.text-overline {\n  text-decoration: line-through;\n}\n.text-trans-orig {\n  text-transform: capitalize;\n  font-size: 16px;\n}\n.title {\n  text-transform: capitalize;\n  margin-bottom: 30px;\n  margin-top: -5px;\n}\n.price {\n  margin-bottom: 25px;\n}\n.price .value {\n  margin-bottom: 0;\n  font-weight: 400;\n  font-family: Arial, Helvetica, sans-serif;\n}\n.wrap-image {\n  display: block;\n  position: relative;\n}\n.wrap-image ul {\n  display: inline-block;\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n.wrap-image ul li {\n  display: block;\n  width: 85px;\n  overflow: hidden;\n  border: solid 1px #efefef;\n  background: #fff;\n  cursor: pointer;\n  position: relative;\n  float: left;\n  margin-right: 5px;\n}\n.wrap-image ul li:before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  border: solid 5px #fff;\n  z-index: 2;\n}\n.wrap-image ul li img {\n  width: 100%;\n  z-index: 1;\n}\nimg.img-detail {\n  width: 100%;\n  border: solid 1px #efefef;\n  background: #fff;\n  padding: 10px;\n}\nbutton {\n  width: 100%;\n}\ntable {\n  margin: 15px 0 25px;\n  text-transform: capitalize;\n}\ntable tr:last-child {\n  border: 0;\n}\ntable tr:nth-child(even) {\n  background: #f9f9f9;\n}\ntable td {\n  padding: 10px 15px;\n  border: solid 1px #efefef;\n}\ntable td:first-child {\n  width: 120px;\n  font-weight: bold;\n}\ntable .label {\n  width: 120px;\n}\np:last-child {\n  margin-bottom: 0;\n}\n/*\nLoading\n=========================== */\n.valign-wrapper {\n  position: relative;\n  width: 100%;\n  top: 0;\n  right: 0;\n  background: #fff;\n}\n.valign-wrapper mat-spinner {\n  width: 50px;\n  height: 50px;\n  display: block;\n  margin: auto;\n}\n/*\nResponsive\n=========================== */\n@media (max-width: 920px) {\n  .wrap-image {\n    padding-left: 0;\n  }\n  .wrap-image ul {\n    display: inline-block;\n    position: relative;\n  }\n  .wrap-image ul li {\n    float: left;\n    display: block;\n    width: 75px;\n    margin-bottom: 5px;\n    margin-right: 5px;\n  }\n  .wrap-image ul li img {\n    width: 100%;\n  }\n\n  img.img-detail {\n    width: auto;\n    max-width: 100%;\n  }\n\n  .price {\n    margin-top: 25px;\n  }\n}\n@media (max-width: 767px) {\n  .value {\n    padding-bottom: 15px;\n    padding-left: 5px;\n  }\n\n  .link {\n    padding-left: 5px;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"