r .row img {\n    max-width: 300px;\n    margin: 0 auto !important;\n  }\n}\n@media (max-width: 767px) {\n  .banner .row .content {\n    text-align: center;\n    padding-top: 0;\n    margin-bottom: 55px;\n  }\n  .banner .row .col {\n    width: 100%;\n  }\n\n  .product-home {\n    padding: 0 25px;\n  }\n}\n@media (max-width: 360px) {\n  .banner .row img {\n    max-width: 200px;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"